import React, { Component  } from 'react'
import { StaticQuery, graphql } from "gatsby"
import { Modal, Button, Form } from 'react-bootstrap';
import WhatsappImg from "../images/whatsapp-logo.png"
import parse from 'html-react-parser';
import $ from "jquery";


export default function Whatsapp () {
  const fnSendMeDetail = (number,pid,pname) => {
    var mobile_no = $("#MobileNo").val();
    var mobile_length = mobile_no.length;

    if (mobile_no == "") {
      $(".form_error").html("Please enter mobile no.");
      return false;
    } else if (mobile_length != 10) {
      $(".form_error").html("Please enter valid mobile no.");
      return false;
    } else {
      $(".form_error").html("");
    }

    $('button').prop('disabled', true);

    var lead = {
      mobile: mobile_no,
      project: pname,
      project_id: pid
    };

    $.ajax({
      type: "post",
      url: 'https://mls-crm.manishalifespaces.com/api/add_lead',
      data: lead,
      success: function (data, status, jqXHR) {
        window.location.href = 'https://api.whatsapp.com/send?phone='+number+'&text=Hello, I am Looking for Codename Enchanted, Please send me more details about this project.';
      },
      error: function (jqXHR, status) {
        $('button').prop('disabled', false);
      }
    });
  }
  
  return (
    <StaticQuery
      query = {graphql`
      query Whatsapp {
          primaryContact: allWpPost(filter: {slug: {eq: "primary-contact"}}) {
            edges {
              node {
                id
                content
              }
            }
          }
          ProjectId:allWpPost(filter: {uri: {}, slug: {eq: "project-id"}}) {
            nodes {
              content
            }
          }
          ProjectName:allWpPost(filter: {uri: {}, slug: {eq: "project-name"}}) {
            nodes {
              content
            }
          }
        }
      `}
      render = { data => (
        
        <>
        <div class="d-flex justify-content-center" style={{ height: '110px', background: '#128c7e', lineHeight: '110px' }}>
          <div>
            <img style={{ width: '50px', margin: '10px' }} src={WhatsappImg} alt='whatsApp' />
          </div>
          <p class="text-white mb-0" style={{ fontSize: '28px' }}>Open WhatsApp</p>
        </div>
        <div class="p-3">
        <p style={{ fontWeight: '400', fontSize: '27px', marginTop: '40px', textAlign: 'center', color: '#555' }}>Chat on WhatsApp with<br /> {parse(data.primaryContact.edges[0].node.content)}</p>
        <Form.Group className="mb-3" controlId="MobileNo">
          <Form.Control type="number" placeholder="Enter Mobile No." name='mobile' style={{ borderColor: 'rgb(228, 228, 228)', height: '50px', color: 'rgb(85, 85, 85)' }} />
          <p class="form_error"></p>
        </Form.Group>
        <p style={{ backgroundColor: '#edf8f4', padding: '18px', margin: '10px 0px', fontSize: '16px', borderRadius: '5px', textAlign: 'center' }}>Hello, I am Looking for Codename Enchanted, Please send me more details about this project.</p>
        <hr style={{ borderTop: '1px solid #eee', background: '#dadde1', borderWidth: '0', color: '#dadde1', height: '2px', margin: '10px 100px' }} />
        <div class="text-center pt-5">
        <button type="button" class="btn" style={{ backgroundColor: '#01e675', borderColor: '#01e675', margin: '20px auto', height: '45px', textTransform: 'uppercase', fontSize: '16px', width: '200px', color: '#fff' }} onClick={() => fnSendMeDetail(parse(data.primaryContact.edges[0].node.content),data.ProjectId.nodes[0].content.replace(/<\/?[^>]+(>|$)/g, ""),data.ProjectName.nodes[0].content.replace(/<\/?[^>]+(>|$)/g, ""))}>Continue to chat </button>
        </div>
        </div>
        </>

      )}
    />
  )
}